<template>
	<za-page-content header="标签分类管理">
		<za-table ref="table" :data="list" @action-change="changePage" highlight-current-row :total="total" @selection-change="handleSelectionChange">
			<el-row class="targetTit" slot="searchBar">
				<el-button size="medium" @click="addNewCatae" type="default">添加分类</el-button>
			</el-row>
			<el-table-column prop="labelCategoryName" label="分类名称"></el-table-column>
			<el-table-column label="操作">
				<template slot-scope="scope">
					<el-button size="mini" type="default" @click="editCate(scope.row)">编辑</el-button>
					<el-button size="mini" type="danger" @click="deleteCate(scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</za-table>
		<el-dialog
			title="添加分类"
			:visible.sync="dialogVisiblePage"
			width="40%">
			<el-row>
				<el-row>
					<el-row class="marginBottom" type="flex" align="middle">
						<el-col :span="4">分类名称</el-col>
						<el-col :span="16" :offset="1">
							<el-input v-model="newCateName"></el-input>
						</el-col>
					</el-row>
				</el-row>
				<div class="dialog-footer" style="margin-top: 30px;">
					<el-button @click="cancelDialog">取 消</el-button>
					<el-button type="primary" @click="saveCate">确 定</el-button>
				</div>
			</el-row>
		</el-dialog>
	</za-page-content>
</template>

<script>
	import {ajaxGetCommon, ajaxPostCommon} from '../../api/common.js'
	import * as ajaxUrl from '../../api/url'

	export default {
		data() {
			return {
				selectionRowData: [],
				dialogVisiblePage: false,
				newCateName: '',
				list: [],
				editCateId: null,
				total: 1,
				pageNum: 1,
				pageSize: 10,
			};
		},
		mounted() {
			this.getList();
		},
		methods: {
			getList() {
				ajaxPostCommon(ajaxUrl.pageInfoLabelCategory, {pageNum: this.pageNum, pageSize: this.pageSize}).then((data) => {
					console.log(data)
					if (data.returnCode === '200') {
						this.list = data.result.list;
						this.total = data.result.total;
					}
				})
			},
			saveCate() {
				if (this.editCateId) {
					this.updateCate();
					return;
				}
				ajaxPostCommon(ajaxUrl.saveInfoLabelCategory, {labelCategoryName: this.newCateName}).then((data) => {
					if (data.returnCode === '200') {
						this.getList();
						this.cancelDialog();
						this.$message({
							message: '添加成功~',
							type: 'success'
						});
					}
				})
			},
			deleteCate(row) {
				console.log(row);
				this.$confirm('确定删除选中标签分类么?', '提示', {
				}).then(()=> {
					ajaxPostCommon(ajaxUrl.deleteInfoLabelCategory, {labelCategoryId: row.labelCategoryId}).then((data) => {
						if (data.returnCode === '200') {
							this.$message({
								message: '删除成功~',
								type: 'success'
							});
							this.getList();
						} else {
							this.$message({
								message: data.returnMsg,
								type: 'error'
							});
						}
					});
				}).catch(()=> {})

			},
			updateCate() {
				ajaxPostCommon(ajaxUrl.updateInfoLabelCategory, {
					labelCategoryName: this.newCateName,
					labelCategoryId: this.editCateId
				}).then((data) => {
					if (data.returnCode === '200') {
						this.cancelDialog();
						this.$message({
							message: '修改成功~',
							type: 'success'
						});
						this.getList();
					}
				})
			},
			addNewCatae() {
				this.newCateName = '';
				this.editCateId = null;
				this.openDialog();
			},
			openDialog() {
				this.dialogVisiblePage = true;
			},
			cancelDialog() {
				this.dialogVisiblePage = false;
			},
			editCate(row) {
				this.newCateName = row.labelCategoryName;
				this.editCateId = row.labelCategoryId;
				this.openDialog();
			},
			changePage(params) {
				this.pageNum = params.page;
				this.pageSize = params.rows || 10;
				this.getList();
			},
			handleSelectionChange(selection) {
				this.selectionRowData = selection
			}
		}
	};
</script>




